(function () {
    'use strict';

    var step10Service = angular.module('journey.module').service('step10Service', ['$rootScope', '$http', 'appState', 'CONFIG', function ($rootScope, $http, appState, CONFIG) {
        var self = this;

        self.setDataInStorage = function (formDetails, callback) {
            // var arnData = angular.merge({}, appState.getApplicationData(), formDetails);
            // appState.setApplicationData(arnData);
            // callback(null, formDetails);
            appState.mergeApplicationData(formDetails);
        };

        self.getDataFromStorage = function () {
            return appState.getApplicationData();
        };


        self.sendSupplementaryData = function (supData, securityParams, isSaveExit) {
            var apiURL, httpMethod;
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                var arn = appState.getApplicationData().primaryApplicant.arn;
                apiURL = CONFIG.apiUrl + '/applicantdetails/supplementarycard/save/' + arn + '/' + isSaveExit + '?hash_id=' + Math.random();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/step10_supplementary_save.json';
            }
            return $http({
                method: httpMethod,
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                },
                data: JSON.stringify(supData),
                params: securityParams
            }).then(function (response) {
                if (response.status === 200 && response.data) {
                    return response.data;
                } else if (response && response.validationErrors) { } else { }
                return;
            }, function (response) {
                return;
            });
        };

        self.getUserDetails = function () {
            var userDTO = {},
                localState = appState.getApplicationData();
            if (localState) {
                if (localState.primaryApplicant) {
                    if (localState.primaryApplicant.dipDetails) {
                        userDTO.balanceTransferTaken = localState.primaryApplicant.dipDetails.balanceTransferTaken === 'Y';
                    }
                    if (localState.primaryApplicant.supplementaryCard) {
                        userDTO.cardHolderName = localState.primaryApplicant.supplementaryCard.cardHolderName ? localState.primaryApplicant.supplementaryCard.cardHolderName : null;
                        userDTO.cardHolderRelationship = localState.primaryApplicant.supplementaryCard.cardHolderRelationship ? localState.primaryApplicant.supplementaryCard.cardHolderRelationship : null;
                        userDTO.dialCodeMobile = localState.primaryApplicant.supplementaryCard.dialCodeMobile ? localState.primaryApplicant.supplementaryCard.dialCodeMobile : null;
                        userDTO.mobile = localState.primaryApplicant.supplementaryCard.mobile ? localState.primaryApplicant.supplementaryCard.mobile : null;
                        userDTO.hasVisitedBefore = localState.primaryApplicant.supplementaryCard.hasVisitedBefore ? localState.primaryApplicant.supplementaryCard.hasVisitedBefore : 'N';
                    }
                }
            }
            return userDTO;
        };

    }]);

    module.exports = step10Service.name;
})();


