(function () {
    'use strict';
    var angular = require('angular');
    require('./step10.component.scss');
    require('./step10.component.service.js');
    // require('../save-exit.modal/overlayConfirmation.modal.component.js');
    require('./add-on-ack/add-on-ack.modal.component.js');

    var step10Comp = angular.module('journey.module').component('step10', {
        template: require('./step10.component.html'),
        controller: ['$rootScope', '$scope', '$uibModal', '$anchorScroll', '$state', 'journeyService', 'step10Service', 'analyticService', 'UtilService',
            function ($rootScope, $scope, $uibModal, $anchorScroll, $state, journeyService, step10Service, analyticService, UtilService) {
                var vm = this;
                // var overlayCheck = '';
                vm.$onInit = function () {
                    $rootScope.$broadcast('hide-step-header', {
                        hideHeader: false
                    });
                    $rootScope.showSelectedCard = true;
                    $rootScope.showARN = true;
                    $rootScope.dipARN = journeyService.getApplicantInfo("dipDetails").dipArn;
                    vm.relationList = ['Brother / Sister', 'Father / Mother', 'Son / Daughter', 'Spouse'];
                    $anchorScroll();
                    $rootScope.stepsHeader.currentStep = 4;
                    vm.selectedCard = journeyService.getSelectedCard();
                    vm.model = {};
                    vm.model = step10Service.getUserDetails();
                    vm.model.dialCodeMobile = "91";
                    journeyService.getErrorMessages().then(function (response) {
                        vm.errorMessages = response.step10;
                    });
                    // overlayCheck = vm.model.hasVisitedBefore ? vm.model.hasVisitedBefore : 'N';                    
                    analyticService.pageList.supplementryCard.internal_reference_id = journeyService.getDataFromStorageContact().adobeId;
                    analyticService.trackPageLoad(analyticService.pageList.supplementryCard);
                };

                vm.goback = function () {
                    if ($scope.step10Form.cardHolderName.$invalid) {
                        vm.model.cardHolderName = $scope.step10Form.cardHolderName.$$rawModelValue;
                    }
                    if ($scope.step10Form.mobile.$invalid) {
                        vm.model.mobile = $scope.step10Form.mobile.$$rawModelValue;
                    }
                    saveFormDetailsInStorage();
                    if (vm.model.balanceTransferTaken) {
                        $state.go('app.journey.balance-transfer',{'cardKey':vm.selectedCard.key});
                    } else {
                        $state.go('app.journey.card-DIP',{'cardKey':vm.selectedCard.key});
                    }
                    analyticService.buttonList.buttonClick.event_content = "back";
                    analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                };

                var sendsaveNav = function (data) {
                    var securityParams = {
                        robocheck: vm.robocheck ? vm.robocheck : ''
                    };
                    step10Service.sendSupplementaryData(data, securityParams, false).then(function (result) {
                        if (result.success) {
                            saveFormDetailsInStorage();
                            if (journeyService.isIncomeDocRequired()) {
                                $state.go('app.journey.document',{'cardKey':vm.selectedCard.key});
                            } else {
                                $state.go('app.journey.signature',{'cardKey':vm.selectedCard.key});
                            }
                        }
                    });
                };

                vm.next = function (isValid) {
                    if (isValid) {
                        var data = {
                            "cardHolderName": vm.model.cardHolderName,
                            "cardHolderRelationship": vm.model.cardHolderRelationship,
                            "dialCodeMobile": vm.model.dialCodeMobile,
                            "mobile": vm.model.mobile,
                            "hasVisitedBefore": vm.model.hasVisitedBefore,
                            "supplementaryCardApplied": "Y"
                        };
                        if (vm.model.hasVisitedBefore === 'Y') {
                            sendsaveNav(data);
                        } else {
                            vm.model.hasVisitedBefore = 'Y';
                            var modalSupplemetaryInstance = $uibModal.open({
                                backdrop: 'static',
                                keyboard: false,
                                modalFade: true,
                                component: "addOnAckModal",
                                windowClass: 'card-detail-modal'
                            });
                            modalSupplemetaryInstance.result.then(function (isSelected) {
                                data.hasVisitedBefore = vm.model.hasVisitedBefore;
                                sendsaveNav(data);
                            });
                        }
                    } else {
                        vm.submitted = true;
                        UtilService.focusOnInvalid("step10Form");
                    }
                    analyticService.buttonList.buttonClick.event_content = "continue";
                    analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                };

                var saveFormDetailsInStorage = function () {
                    var formDetails = {
                        "primaryApplicant": {
                            "supplementaryCard": {
                                "cardHolderName": vm.model.cardHolderName,
                                "cardHolderRelationship": vm.model.cardHolderRelationship,
                                "dialCodeMobile": vm.model.dialCodeMobile,
                                "mobile": vm.model.mobile,
                                "hasVisitedBefore": vm.model.hasVisitedBefore
                            }
                        }
                    };
                    step10Service.setDataInStorage(formDetails);
                };

                vm.save = function () {
                    var modalInstance = $uibModal.open({
                        backdrop: 'static',
                        keyboard: false,
                        modalFade: true,
                        component: "overlaySaveConfirmation",
                        windowClass: 'card-detail-modal'
                    });
                    modalInstance.result.then(function (isSelected) {
                        if (isSelected) {
                            var data = {
                                "cardHolderName": vm.model.cardHolderName,
                                "cardHolderRelationship": vm.model.cardHolderRelationship,
                                "dialCodeMobile": vm.model.dialCodeMobile,
                                "mobile": vm.model.mobile,
                                "hasVisitedBefore": vm.model.hasVisitedBefore,
                                "supplementaryCardApplied": "Y"
                            };
                            var securityParams = {
                                robocheck: vm.robocheck ? vm.robocheck : ''
                            };
                            step10Service.sendSupplementaryData(data, securityParams, true).then(function (result) {
                                if (result.success) {
                                    saveFormDetailsInStorage();
                                    $state.go('app.save-exit', {
                                        saveData: {
                                            name: journeyService.getApplicantInfo("fullName")
                                        },cardKey:vm.selectedCard.key
                                    });
                                }
                            });
                        }
                    }, function () { });
                    analyticService.buttonList.buttonClick.event_content = "save for later";
                    analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                };
            }]
    });

    module.exports = step10Comp.name;
})();


